ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
.menu_right_form {
    padding: 20px 80px 20px 40px;
    border: 1px dashed #a3a3a3;
  }
 
 .menu_right_form li ul{
    position: relative;
    left: 25px;
 }
 .menu_right_form li ul li{
    position: relative;
    z-index: 1;
 }
 .menu_right_form li ul li::before{
    content: "";
    width: 16px;
    height: 54px;
    position: absolute;
    bottom: 20px;
    left: -16px;
    border-left: 1px solid #888888;
    border-bottom: 1px solid #888888;
    z-index: -1;
 }
 
 .menu_right_form h2{
    font-size: 17px;
    color: #a8a8a8;
    text-align: center;
    margin-bottom: 20px;
 }
 .menuOrderingEach {
    color: #333333;
    font-weight: 500;
    background: #f2f2f2;
    padding: 13px 20px;
    display: block;
    white-space: nowrap;
    position: relative;
    z-index: 2;
 }
 .menuOrderingEach.hide_show{
    background: #f1f1f1;
 }
 .menuOrderingEach .menuVisibility{
    width: 50px;
    height: 45px;
    position: absolute;
    top: 0;
    right: 0;
    background: #f7f7f7;
    text-align: center;
    line-height: 45px;
 }
 .leftMenuList li + li, .menu_right_form li {
    margin-top: 8px;
}
.leftMenuList li a{
    color: #333333;
    font-weight: 500;
    background: #f7f7f7;
    padding: 13px 20px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

 }
 .addToMenuBox{
    background: #f2f2f2;
    padding: 30px;
 }